class MiniShuffleGraphic extends React.Component {

  drawCanvas() {
    const dotRadius = 3;
    const lineWidth = 2;
    const blue = "#3f51b5";
    const grey = '#bdbdbd';
    let perms = this.props.permutations;
    let n = perms[0].length;
    const cs = this.props.currentSentence;

    const canvas = this.refs.canvas;
    const ctx = canvas.getContext("2d");

    
    ctx.strokeStyle = grey;
    ctx.fillStyle = grey;
    ctx.lineWidth = lineWidth;
    ctx.lineCap = "round";
    ctx.lineJoin = "round";
    for (let i = 0; i < n; i++) { 
      if ( cs != i) {
        ctx.beginPath();
        let p0 = this.transform(i/(n-1), 0);
        ctx.moveTo(p0.x, p0.y);
        let x1 = perms[0][i]/(n-1);
        let p1 = this.transform(x1, 1/3);
        ctx.lineTo(p1.x, p1.y);
        let x2 = perms[1][perms[0][i]]/(n-1);
        let p2 = this.transform(x2, 2/3);
        ctx.lineTo(p2.x, p2.y);
        let x3 = perms[2][perms[1][perms[0][i]]]/(n-1);
        let p3 = this.transform(x3, 1);
        ctx.lineTo(p3.x, p3.y);
        ctx.stroke();

        ctx.beginPath();
        ctx.arc(p0.x, p0.y, dotRadius, 0, 2 * Math.PI);
        ctx.fill();
        ctx.beginPath();
        ctx.arc(p1.x, p1.y, dotRadius, 0, 2 * Math.PI);
        ctx.fill();
        ctx.beginPath();
        ctx.arc(p2.x, p2.y, dotRadius, 0, 2 * Math.PI);
        ctx.fill();
        ctx.beginPath();
        ctx.arc(p3.x, p3.y, dotRadius, 0, 2 * Math.PI);
        ctx.fill();
      }
    }

    ctx.strokeStyle = blue;
    ctx.fillStyle = blue;
    ctx.beginPath();
    let p0 = this.transform(cs/(n-1), 0);
    ctx.moveTo(p0.x, p0.y);
    let x1 = perms[0][cs]/(n-1);
    let p1 = this.transform(x1, 1/3);
    ctx.lineTo(p1.x, p1.y);
    let x2 = perms[1][perms[0][cs]]/(n-1);
    let p2 = this.transform(x2, 2/3);
    ctx.lineTo(p2.x, p2.y);
    let x3 = perms[2][perms[1][perms[0][cs]]]/(n-1);
    let p3 = this.transform(x3, 1);
    ctx.lineTo(p3.x, p3.y);
    ctx.stroke();

    ctx.beginPath();
    ctx.arc(p0.x, p0.y, dotRadius, 0, 2 * Math.PI);
    ctx.fill();
    ctx.beginPath();
    ctx.arc(p1.x, p1.y, dotRadius, 0, 2 * Math.PI);
    ctx.fill();
    ctx.beginPath();
    ctx.arc(p2.x, p2.y, dotRadius, 0, 2 * Math.PI);
    ctx.fill();
    ctx.beginPath();
    ctx.arc(p3.x, p3.y, dotRadius, 0, 2 * Math.PI);
    ctx.fill();
  }

  componentDidUpdate() {
    this.drawCanvas();
  }
 
  componentDidMount() {
    this.drawCanvas()
    
  }

  transform(x, y) {
    const padding = 15;
    const xScale = this.props.width - (2 * padding);
    const yScale = this.props.height - (2 * padding);
    return({x: x*xScale + padding, y: y*yScale + padding})
  }

  render() {
    return(
      <Zoom in={true}>
      <div>
        <canvas ref="canvas" width={this.props.width} height={this.props.height}/>
      </div>
      </Zoom>
    )
  }
}

class ShuffleGraphic extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      isVisible: true
    }
  }

  hide() {
    this.setState({isVisible: false});
  }
 
  componentDidMount() {
    setTimeout(this.hide.bind(this), 2000)
    const lineWidth = 4;
    const dotRadius = 7;
    const color = "#3f51b5";
    let perms = this.props.permutations;
    let n = perms[0].length;
    

    const canvas = this.refs.canvas;
    const ctx = canvas.getContext("2d");
    ctx.fillStyle = color;
    
    for (let i = 0; i < n; i++) {
      for (let j = 0; j < 4; j++) {
        ctx.beginPath();
        let p = this.transform(i/(n-1), j/3);
        ctx.arc(p.x, p.y, dotRadius, 0, 2 * Math.PI);
        ctx.fill();
      }
    }


    ctx.beginPath();
    ctx.strokeStyle = color;
    ctx.lineWidth = lineWidth;
    for (let i = 0; i < n; i++) {
      let p0 = this.transform(i/(n-1), 0);
      ctx.moveTo(p0.x, p0.y);
      let x1 = perms[0][i]/(n-1);
      let p1 = this.transform(x1, 1/3);
      ctx.lineTo(p1.x, p1.y);
      let x2 = perms[1][perms[0][i]]/(n-1);
      let p2 = this.transform(x2, 2/3);
      ctx.lineTo(p2.x, p2.y);
      let x3 = perms[2][perms[1][perms[0][i]]]/(n-1);
      let p3 = this.transform(x3, 1);
      ctx.lineTo(p3.x, p3.y);
    }
    ctx.stroke();
  }

  transform(x, y) {
    const padding = 30;
    const xScale = this.props.width - (2 * padding);
    const yScale = this.props.height - (2 * padding);
    return({x: x*xScale + padding, y: y*yScale + padding})
  }

  render() {
    return(
      <Zoom in={this.state.isVisible}>
      <div>
        <canvas ref="canvas" width={this.props.width} height={this.props.height}/>
      </div>
      </Zoom>
    )
  }
}

