class ViewSentences extends React.Component {
  constructor(props) {
    super(props)
    this.state = {
      sentences: [],
      intervalID: null,
      codeWasValid: false,
      showAuthors: false,
      currentSentence: 0,
      permutations: [],
      showingGraphic: true
    };
  }

  makeSentencePapers() {
    let sentence = this.state.sentences[this.state.currentSentence];
    let sentencePaper;
    if (!this.state.showAuthors) {
      sentencePaper =
        <Zoom in={true}>
        <div className="single-response">
          <Typography variant="h5" align="center">
            {sentence.who.response.trim() + " " +
            sentence.what.response.trim() + " " +
            sentence.where.response.trim() + " " +
            sentence.why.response.trim()}
          </Typography>
        </div>
        </Zoom>;
    } else {
      sentencePaper =
      <>
        <div className="single-response">
          <Zoom in={this.state.showAuthors}>
            <Typography variant="h5" align="center">{sentence.who.response}</Typography>
          </Zoom>
          <Zoom in={this.state.showAuthors} style={{ transitionDelay: '100ms' }}>
            <Chip variant="outlined" size="small" label={sentence.who.name} color="primary"/>
          </Zoom>
        </div>
        
        <div className="single-response">
          <Zoom in={this.state.showAuthors}>
            <Typography variant="h5" align="center">{sentence.what.response}</Typography>
          </Zoom>
          <Zoom in={this.state.showAuthors} style={{ transitionDelay: '200ms' }}>
            <Chip variant="outlined" size="small" label={sentence.what.name} color="primary"/>
          </Zoom>
        </div>

        <div className="single-response">
          <Zoom in={this.state.showAuthors}>
            <Typography variant="h5" align="center">{sentence.where.response}</Typography>
          </Zoom>
          <Zoom in={this.state.showAuthors} style={{ transitionDelay: '300ms' }}>
            <Chip variant="outlined" size="small" label={sentence.where.name} color="primary"/>
          </Zoom>
        </div>

        <div className="single-response">
          <Zoom in={this.state.showAuthors}>
            <Typography variant="h5" align="center">{sentence.why.response}</Typography>
          </Zoom>
          <Zoom in={this.state.showAuthors} style={{ transitionDelay: '400ms' }}>
            <Chip variant="outlined" size="small" label={sentence.why.name} color="primary"/>
          </Zoom>
        </div>
      </>;
    }
    return(sentencePaper);
  }

  componentDidMount() {
    this.getData();
  }

  componentWillUnmount() {
    clearTimeout(this.state.intervalID);
  }

  hideGraphic() {
    this.setState({showingGraphic: false})
  }

  reshuffle() {
    this.setState({isLoaded: false});
    let body = JSON.stringify(
      {
        type: "create-sentences",
        code: this.props.gameCode
      }
    );
    const request = {
      method: 'POST',
      headers: {
        'Content-Type': 'application/json'
      },
      body: body
    };
    fetch('https://us-central1-to-do-list-266601.cloudfunctions.net/wwww', request)
      .then(res => res.json())
      .then(
        (result) => {
          if (result.codeWasValid) {
            this.setState({codeWasValid: true});
            if (result.gameState.gameStage == "sentences-created") {
              this.setState({
                showingGraphic: true,
                sentences: result.gameState.sentences,
                permutations: result.gameState.permutations,
                isLoaded: true
              });
              setTimeout(this.hideGraphic.bind(this), 3000);
            } 
          } else {
            this.setState({
              isLoaded: true,
              codeWasValid: false
            });
          }
        },
        (error) => {
          this.setState({
            isLoaded: true,
            error: error
          });
        }
      );
  }

  getData() {
    let body = JSON.stringify(
      {
        type: "get-game-state",
        code: this.props.gameCode
      }
    );
    const request = {
      method: 'POST',
      headers: {
        'Content-Type': 'application/json'
      },
      body: body
    };
    console.log('request sent from ViewSentences/getData');
    fetch('https://us-central1-to-do-list-266601.cloudfunctions.net/wwww', request)
      .then(res => res.json())
      .then(
        (result) => {
          if (result.codeWasValid) {
            this.setState({codeWasValid: true});
            if (result.gameState.gameStage == "sentences-created") {
              this.setState({
                sentences: result.gameState.sentences,
                permutations: result.gameState.permutations,
                isLoaded: true
              });
              clearTimeout(this.state.intervalID);
              setTimeout(this.hideGraphic.bind(this), 3000);
            } else {
              let intervalID = setTimeout(this.getData.bind(this), 4000);
              this.setState({intervalID: intervalID});
            }
          } else {
            this.setState({codeWasValid: false});
          }
        },
        (error) => {
          this.setState({
            isLoaded: true,
            error: error
          });
        }
      );
  }

  handleSwitch() {
    this.setState({showAuthors: !this.state.showAuthors});
  }

  navigate(i) {
    const newIndex = (this.state.currentSentence + i) % this.state.sentences.length;
    this.setState({currentSentence: newIndex});
  }

  render() {
    if (this.state.error) {
      return (<div>Error: {this.state.error.message}</div>);
    } else if (!this.state.isLoaded) {
      return(<CircularProgress />);
    } else {
      if (this.state.codeWasValid) {
        if (this.state.showingGraphic) {
          console.log(this.state.permutations)
          return(
            <ShuffleGraphic width={250} height={250} permutations={this.state.permutations}/>
          );
        } else {
          return(
            <div className="prompt-text">

                <div className="show-authors">
                  <FormControlLabel 
                    control={
                      <Switch
                        checked={this.state.showAuthors}
                        onChange={() => this.handleSwitch()}
                        color="primary"
                      />
                    }
                    label="Show Authors"
                  />
                  <IconButton onClick={() => this.reshuffle()}>
                    <ShuffleIcon />
                  </IconButton>
                </div>
                
                <div className="sentence">
                  <div className="sentence-inside" >{this.makeSentencePapers()}</div>
                </div>
                
                <div className="bottom-nav">
                  <IconButton color="primary" onClick={() => this.navigate(this.state.sentences.length - 1)}>
                    <NavigateBeforeIcon />
                  </IconButton>
                  <Typography variant="body1" align="center">
                    {(this.state.currentSentence+1) + "/" + this.state.sentences.length}
                  </Typography>
                  <IconButton color="primary" onClick={() => this.navigate(1)}>
                    <NavigateNextIcon />
                  </IconButton>
                </div>

                <div className="mini-graphic">
                  <MiniShuffleGraphic 
                    width={70} 
                    height={70} 
                    permutations={this.state.permutations}
                    currentSentence={this.state.currentSentence}
                  />
                </div>

            </div>
          )
        }
      } else {

      }
    }
  }
}

class FinishedResponses extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      players: [],
      isLoaded: false,
      codeWasValid: false,
      intervalID: null
    }
  }

  componentDidMount() {
    this.getData();
  }

  componentWillUnmount() {
    clearTimeout(this.state.intervalID);
  }

  getSentences() {
    clearTimeout(this.state.intervalID);
    this.props.getSentences();
  }

  getData() {
    let body = JSON.stringify(
      {
        type: "get-game-state",
        code: this.props.gameCode
      }
    );
    const request = {
      method: 'POST',
      headers: {
        'Content-Type': 'application/json'
      },
      body: body
    };
    console.log('request sent from FinishedResponses/getData');
    fetch('https://us-central1-to-do-list-266601.cloudfunctions.net/wwww', request)
      .then(res => res.json())
      .then(
        (result) => {
          if (result.codeWasValid) {
            if (result.gameState.gameStage == "all-players-waiting" || result.gameState.gameStage == 'sentences-created') {
              this.getSentences();
            } else {
              let intervalID = setTimeout(this.getData.bind(this), 4000);
              this.setState({
                players: result.gameState.players,
                intervalID: intervalID
              });
              if(!this.state.isLoaded) {
                this.setState({
                  isLoaded: true,
                  codeWasValid: true
                })
              }
            } 
          } else {
            this.setState({
              codeWasValid: false,
              isLoaded: true
            });
          }
        },
        (error) => {
          this.setState({
            isLoaded: true,
            error: error
          });
        }
      );
  }

  createPlayers() {
    let players = Array();
    for (let [index, player] of this.state.players.entries()) {
      let icon;
      if (player.doneWithQuestions) {
        icon = <CheckCircleIcon/>
      } else {
        icon = <CreateIcon/>
      }
      let time = (100 * (index+1)) + 'ms';
      players.push(
        <Grid item>
          <Zoom in={true} style={{ transitionDelay: time }}>
            <Chip icon={icon} label={player.name} color="primary"/>
          </Zoom>
        </Grid>
      );
    }
    return(players);
  }

  render() {
    
    if (this.state.error) {
      return (<div>Error: {this.state.error.message}</div>);
    } else if (!this.state.isLoaded) {
      return(<CircularProgress />);
    } else {
      if (this.state.codeWasValid) {
        return(
          <Slide in={true} mountOnEnter unmountOnExit>
          <div className="prompt-text">
            <Grid container direction="column" spacing={5} justify="center" alignItems="center">
              <Grid item>
                <Typography variant="h4" align="center">
                  Waiting for all players to finish
                </Typography>
              </Grid>

              <Grid item>
                <Grid container direction="row" spacing={2} justify="center" alignItems="center">
                  {this.createPlayers()}
                </Grid>
              </Grid>

            </Grid>
          </div>
          </Slide>
        );
      } else{
        return(
          <div className="prompt-text">
            <Grid container direction="column" spacing={4} justify="center" alignItems="center">
              <Grid item>
                <Typography variant="h5" align="center" >
                  looks like your game no longer exists
                </Typography>
              </Grid>
              <Grid item>
                <Button 
                  onClick={() => this.props.enterNewCode()}
                  variant="contained" 
                  color="primary">
                  Enter Another Code
                </Button>
              </Grid>           
            </Grid>
          </div>
        );
      }
    }
  }
}

class EnterResponses extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      response: {
        name: props.name,
        who: "",
        what: "",
        where: "",
        why: ""
      },
      currentQuestion: "who"
    }
  }

  handleResponseChange(e) {
    let response = {
      name: this.props.name,
      who: this.state.response.who,
      what: this.state.response.what,
      where: this.state.response.where,
      why: this.state.response.why
    };
    if (this.state.currentQuestion == "who") {
      response.who = e.target.value;
    } else if (this.state.currentQuestion == "what") {
      response.what = e.target.value;
    } else if (this.state.currentQuestion == "where") {
      response.where = e.target.value;
    } else if (this.state.currentQuestion == "why") {
      response.why = e.target.value;
    }
    this.setState({
      response: response
    })
  }

  handleNext() {
    if (this.state.currentQuestion == "who") {
      this.setState({currentQuestion: "what"});
    } else if (this.state.currentQuestion == "what") {
      this.setState({currentQuestion: "where"});
    } else if (this.state.currentQuestion == "where") {
      this.setState({currentQuestion: "why"});
    } else if (this.state.currentQuestion == "why") {
      this.props.sendResponse(this.state.response);
    }
  }

  render() {
    let question;
    let explanation;
    let example;
    let pronounStuff;
    let responseText;
    if (this.state.currentQuestion == "who") {
      responseText = this.state.response.who
      question = "WHO";
      explanation = "enter any person or animal etc";
      example = " eg. 'Obama' or 'Obama's dog, Bo'";
      pronounStuff = <div></div>;
    } else if (this.state.currentQuestion == "what") {
      responseText = this.state.response.what
      question = "WHAT";
      explanation = "enter an action";
      example = " eg. 'went for a bike ride' or 'slept'";
      pronounStuff = 
        <Typography variant="body1">
          tip: if you want to write something like 'went to HER appartment', you could enter
          'went to THEIR appartment' instead to make the final sentence make more sense. 
          (they / them / their / theirs / themself)
        </Typography>;
    } else if (this.state.currentQuestion == "where") {
      responseText = this.state.response.where
      question = "WHERE";
      explanation = "enter where, when or how the thing happened "
      example = " eg. 'on their birthday' or 'with Batman'";
      pronounStuff = 
        <Typography variant="body1">
          tip: if you want to write something like 'with HIS uncle', you could enter
          'with THEIR uncle' instead to make the final sentence make more sense. 
          (they / them / their / theirs / themself)
        </Typography>;
    } else if (this.state.currentQuestion == "why") {
      responseText = this.state.response.why
      question = "WHY"
      explanation = "enter an explanation or other clause";
      example = " eg. 'because they wanted to.' or 'in order to protect baby yoda.'";
      pronounStuff =
        <Typography variant="body1">
          tip: if you want to write something like 'because SHE WAS sad', you could enter
          'because THEY WERE sad' instead to make the final sentence make more sense. 
          (they / them / their / theirs / themself)
        </Typography>
    }
    return(
      <Slide in={true} mountOnEnter unmountOnExit>
      <div className="prompt-text">
        <Grid container direction="column" spacing={5} justify="center" alignItems="center">

          <Grid item>
            <Grid container direction="column" spacing={1} justify="center" alignItems="center">
              <Grid item>
                <Typography variant="h3" align="center">{question}</Typography>
              </Grid>
              <Grid item>
                <Typography variant="body1" align="center">{explanation}</Typography>
              </Grid>
              <Grid item>
                <Typography variant="body2" align="center">{example}</Typography>
              </Grid>
            </Grid>
          </Grid>

          <Grid item>
            <TextField 
              id="standard-basic" 
              label={question} 
              multiline
              onChange={(event) => this.handleResponseChange(event)}
              value={responseText}
            />
          </Grid>

          <Grid item>
            <Button 
              onClick={() => this.handleNext()}
              variant="contained" 
              color="primary" 
              endIcon={<NavigateNextIcon/>}>
                Next
            </Button>
          </Grid>

        </Grid>
      </div>
      </Slide>
    );
  }
}

class PlayerWaitingRoom extends React.Component {
  constructor(props) {
    super(props);

    this.state = {
      error: null,
      isLoaded: false,
      players: [],
      codeWasValid: false,
      intervalID: null
    }
  }

  componentDidMount() {
    if (!this.props.gameJoined) {
      this.joinGame();
    } else {
      this.getData();
    }
  }

  componentWillUnmount() {
    clearTimeout(this.state.intervalID);
  }

  joinGame() {
    let body = JSON.stringify(
      {
        type: "enter-waiting-room",
        name: this.props.name,
        code: this.props.gameCode
      }
    );
    const request = {
      method: 'POST',
      headers: {
        'Content-Type': 'application/json'
      },
      body: body
    };
    console.log('request sent from PlayerWaitingRoom/joinGame');
    fetch('https://us-central1-to-do-list-266601.cloudfunctions.net/wwww', request)
      .then(res => res.json())
      .then(
        (result) => {
          if (result.codeWasValid) {
            this.props.joinGame(result.playerId);
            this.setState({
              isLoaded: true,
              players: result.gameState.players,
              codeWasValid: true,
            });
            this.getPlayerData()
          } else {
            this.props.noGameFound();
          }
        },
        (error) => {
          this.setState({
            isLoaded: true,
            error: error
          });
        }
      );
  }

  goToResponses() {
    clearTimeout(this.state.intervalID);
    this.props.goToResponses()
  }

  getPlayerData() {
    let body = JSON.stringify(
      {
        type: "get-game-state",
        code: this.props.gameCode
      }
    );
    const request = {
      method: 'POST',
      headers: {
        'Content-Type': 'application/json'
      },
      body: body
    };
    console.log('request sent from PlayerWaitingRoom/getPlayerData');
    fetch('https://us-central1-to-do-list-266601.cloudfunctions.net/wwww', request)
      .then(res => res.json())
      .then(
        (result) => {
          if (result.codeWasValid) {
            let intervalID = setTimeout(this.getPlayerData.bind(this), 4000);
            this.setState({
              players: result.gameState.players,
              intervalID: intervalID
            });
            if (result.gameState.gameStage == "in-progress") {
              this.goToResponses();
            } 
            
          } else {
            this.setState({
              codeWasValid: false
            });
          }
        },
        (error) => {
          this.setState({
            error: error
          });
        }
      );
  }

  createPlayers() {
    let players = Array();
    for (let [index, player] of this.state.players.entries()) {
      let time = (100 * (index+1)) + 'ms';
      if (player.id == "1") {
        players.push(
          <Grid item>
            <Zoom in={true} style={{ transitionDelay: time }}>
            <Badge badgeContent="Host" color="secondary">
              <Chip label={player.name} color="primary"/>
            </Badge>
            </Zoom>
          </Grid>
        );
      } else {
        players.push(
          <Grid item>
            <Zoom in={true} style={{ transitionDelay: time }}>
            <Chip label={player.name} color="primary"/>
            </Zoom>
          </Grid>
        );
      }
    }
    return(players);
  }

  render() {
    if (this.state.error) {
      return (<div>Error: {this.state.error.message}</div>);
    } else if (!this.state.isLoaded) {
      return(<CircularProgress />);
    } else {
      if (this.state.codeWasValid) {
        return(
          <Slide in={true} mountOnEnter unmountOnExit>
          <div className="prompt-text">
            <Grid container direction="column" spacing={5} justify="center" alignItems="center">

              <Grid item>
                <Typography variant="h4" align="center" >
                  You're in the waiting room! 
                </Typography>
              </Grid>

              <Grid item>
                <Grid container direction="column" spacing={2} justify="center" alignItems="center">
                  <Grid item>
                    <Typography variant="h5" align="center" >players:</Typography>
                  </Grid>
                  <Grid item>
                    <Grid container direction="row" spacing={2} justify="center" alignItems="center">
                      {this.createPlayers()}
                    </Grid>
                  </Grid>
                </Grid>
              </Grid>

              <Grid item>
                <Typography variant="h5" align="center" >
                  {"game: " + this.props.gameCode}
                </Typography>
              </Grid>

            </Grid>
          </div>
          </Slide>
        );
      } else {
        return(
          <div className="prompt-text">
            <Grid container direction="column" spacing={4} justify="center" alignItems="center">
              <Grid item>
                <Typography variant="h5" align="center" >
                  That game no longer exists (the host could have lost connection)
                </Typography>
              </Grid>
              <Grid item>
              <Button 
                onClick={() => this.props.enterNewCode()}
                variant="contained" 
                color="primary" >
                Enter Another Code
              </Button>

              </Grid>
              
            </Grid>
          </div>
        );
      }
    }
  }
}

class HostWaitingRoom extends React.Component {
  constructor(props) {
    super(props);

    this.state = {
      error: null,
      isLoaded: false,
      players: Array(),
      code: "",
      callCounter: 0,
      codeExpired: false,
      intervalID: null
    }
  }

  componentWillUnmount() {
    clearTimeout(this.state.intervalID);
  }

  componentDidMount() {
    if (!this.props.gameCreated) {
      this.createWaitingRoom();
    } else {
      this.getData();
    }
  }

  createWaitingRoom() {
    let body = JSON.stringify(
      {
        type: "create-waiting-room",
        name: this.props.name
      }
    );
    const request = {
      method: 'POST',
      headers: {
        'Content-Type': 'application/json'
      },
      body: body
    };
    console.log('request sent from HostWaitingRoom/createWaitingRoom');
    fetch('https://us-central1-to-do-list-266601.cloudfunctions.net/wwww', request)
      .then(res => res.json())
      .then(
        (result) => {
          this.props.createGame(result.gameState, result.playerId);
          let intervalID = setTimeout(this.getData.bind(this), 4000);
          this.setState({
            isLoaded: true,
            code: result.gameState.code,
            players: result.gameState.players,
            intervalID: intervalID
          });
        },
        (error) => {
          this.setState({
            isLoaded: true,
            error: error
          });
        }
      );
  }

  getData() {
    let body = JSON.stringify(
      {
        type: "get-game-state",
        code: this.state.code
      }
    )
    const request = {
      method: 'POST',
      headers: {
        'Content-Type': 'application/json'
      },
      body: body
    };
    console.log('request sent from HostWaitingRoom/getData');
    fetch('https://us-central1-to-do-list-266601.cloudfunctions.net/wwww', request)
      .then(res => res.json())
      .then(
        (result) => {
          this.setState({
            players: result.gameState.players,
            callCounter: this.state.callCounter + 1
          });
          if (this.state.callCounter < 600) {
            let intervalID = setTimeout(this.getData.bind(this), 4000);
            this.setState({intervalID: intervalID});
          } else {
            this.setState({
              codeExpired: true
            })
            this.props.endGame();
          }
        },
        (error) => {
          this.setState({
            error: error
          });
        }
      );
  }

  newCode() {
    this.setState({
      codeExpired: false,
      callCounter: 0,
      isLoaded: false,
    });
    this.createWaitingRoom()
  }

  createPlayers() {
    let players = Array();
    for (let [index, player] of this.state.players.entries()) {
      let time = (100 * (index+1)) + 'ms';
      if (player.id == "1") {
        players.push(
            <div className="player-chip">
              <Zoom in={true} style={{ transitionDelay: time }}>
              <Badge badgeContent="Host" color="secondary">
                <Chip label={player.name} color="primary"/>
              </Badge>
              </Zoom>
            </div>
        );
        
      } else {
        players.push(
          <div className="player-chip">
            <Zoom in={true} style={{ transitionDelay: time }}>
            <Chip label={player.name} color="primary"/>
            </Zoom>
          </div>
        )
      }
    }
    return(<div className="player-list">{players}</div>)
  }

  startGame() {
    clearTimeout(this.state.intervalID);
    this.props.startGame();
  }

  render() {
    if (this.state.error) {
      return (<div>Error: {this.state.error.message}</div>);
    } else if (!this.state.isLoaded) {
      return(<CircularProgress />);
    } else {
      if (!this.state.codeExpired) {
        let button;
        if (this.state.players.length > 1) {
          button = 
            <Button 
              onClick={() => this.startGame()}
              variant="contained" 
              color="primary" >
              Start Game
            </Button>
        } else {
          button = 
            <Button 
              disabled
              onClick={() => this.startGame()}
              variant="contained" 
              color="primary" >
              Start Game
            </Button>
        }
        return(
          <Slide in={true} mountOnEnter unmountOnExit>
          <div className="flex-column">
            <div className="margin10">
              <div className="margin5"><Typography variant="h5" align="center" >Your Game Code is:</Typography></div>

              <div className="margin5"><Typography variant="h3" align="center" >{this.state.code}</Typography></div>
            </div>

            <div className="margin10">
              <div className="margin5"><Typography variant="h5" align="center" >players:</Typography></div>

              <div className="margin10">{this.createPlayers()}</div>
            </div>

            <div className="margin10">{button}</div>
           </div>
           </Slide>

        );
      } else {
        return(
          <div className="prompt-text">
            <Grid container direction="column" spacing={5} justify="center" alignItems="center">
              <Grid item>
                <Typography variant="h5" align="center" >Your game code has expired</Typography>
              </Grid>
              <Grid item>
              <Button 
                  onClick={() => this.newCode()}
                  variant="contained" 
                  color="primary" 
                  >
                    Get New Code
                </Button>
              </Grid>
            </Grid>
          </div>
        );
      }
    }
  }
}

class EnterCode extends React.Component {

  constructor(props) {
    super(props);
    let message;
    if (props.invalid) {
      message = "Sorry " + props.playerName + ", the code you entered is invalid.";
    } else {
      message = "How's it goin " + props.playerName + ", Enter your game's 4 letter code";
    }
    this.state = {
      code: '',
      message: message
    };
    props.handleCodeChange('');
  }

  handleCodeChange(event) {
    let code = String(event.target.value);
    code = code.toUpperCase();
    if (code.length > 4) {
      code = code.slice(0, 4);
    }
    this.setState({code: code});
    this.props.handleCodeChange(code);
  }

  render() {

    let input;
    if (!this.props.invalid) {
      input = <TextField 
        id="standard-basic" 
        label="code" 
        onChange={(e) => this.handleCodeChange(e)}
        value={this.state.code}
      />
    } else {
      input = <TextField 
        error
        id="standard-basic" 
        label="invalid code" 
        onChange={(e) => this.handleCodeChange(e)}
        value={this.state.code}
      />
    }

    return(
      <Zoom in={true}>
        <div className="flex-column">
        <div className="margin15 prompt-text">
          <Typography variant="h5" align="center" >{this.state.message}</Typography>
        </div>

        <div className="margin15">{input}</div>

        <div className="back-next-buttons">
          <div className="back-button">
            <Button 
              onClick={this.props.handleBack}
              variant="contained" 
              color="primary" 
              startIcon={<NavigateBeforeIcon/>}>
                Back 
            </Button>
          </div>
          <div className="next-button">
            <Button 
              onClick={this.props.handleNext}
              variant="contained" 
              color="primary" 
              endIcon={<NavigateNextIcon/>}>
                Next
            </Button>
          </div>
        </div>
        </div>
      </Zoom>
    );
  }
  
}

